const utilFunc = {
    initIconFont () {
        const domModule = weex.requireModule('dom')
        domModule.addRule('fontFace', {
            'fontFamily': "iconfont",
            'src': "url('http://at.alicdn.com/t/font_712458_9z7lk83avy6.ttf')"
        })
    }
};

export default utilFunc;



