<template>
    <div class="tab-wrapper">
        <div class="tab=item" @click="tabSwitch('home')">
            <text class="tab-icon iconfont">&#xe64f;</text>
            <text class="tab-txt"></text>
        </div>
        <div class="tab=item" @click="tabSwitch('class')">
            <text class="tab-icon iconfont">&#xe684;</text>
            <text class="tab-txt"></text>
        </div>
        <div class="tab=item" @click="tabSwitch('shop')">
            <text class="tab-icon iconfont">&#xe63f;</text>
            <text class="tab-txt"></text>
        </div>
        <div class="tab=item" @click="tabSwitch('my')">
            <text class="tab-icon iconfont">&#xe659;</text>
            <text class="tab-txt"></text>
        </div>
    </div>
</template>
<script>
export default {
  methods: {
      tabSwitch (index) {
          this.$emit("tabSwitch", {data: index});
      }
  }
}
</script>
<style lang="stylus" scoped>
    .iconfont 
        font-family: iconfont;
    .tab-wrapper
        display: flex;
        width: 100%;
        height: 90px;
        flex-direction: row;
        justify-content: space-around;
        border-top-width: 1px;
        border-top-color: #d9d9d9;
        background-color: #fafafa;
        .tab-item
            flex: 1;
            text-align: center;
            .tab-icon
                color: #666;
                font-size: 38px;
                text-align: center;
            .tab-txt
                color: #666;
</style>



