import Vue from 'vue'
/* global Vue */
import Router from 'vue-router'
import ViewHome from '@/components/ViewHome'
import ViewClass from '@/components/ViewClass'
import ViewShop from '@/components/ViewShop'
import ViewMy from '@/components/ViewMy'

Vue.use(Router)

module.exports = new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'ViewHome',
      component: ViewHome
    },
    {
      path: '/class',
      name: 'ViewClass',
      component: ViewClass
    },
    {
      path: '/shop',
      name: 'ViewShop',
      component: ViewShop
    },
    {
      path: '/my',
      name: 'ViewMy',
      component: ViewMy
    }
  ]
})



